:root {
  --bg-button: #f0f0f0;
  --border-button: #888;
}

.btn {
  font-size: 1.4em;
  background-color: var(--bg-button);
  border: 0;
  border-right: 1px solid var(--border-button);
  border-bottom: 1px solid var(--border-button);
  outline: none;
}

.btn:active {
  background-color: #ccc;
}

.btn.double {
  grid-column: span 2;
}

.btn.triple {
  grid-column: span 3;
}

.btn.operation {
  background-color: #fa8231;
  color: #fff;
}

.btn.operation:active {
  background-color: #fa8231cc;
}
